<template>
	<div class="jbm__sidebar">
		<div class="jbm__heading">
			<figure>
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 197.61 169" width="22" height="19"><circle style="fill:none;stroke:#fff;stroke-linecap:round;stroke-linejoin:round;stroke-width:9px;" cx="73.67" cy="73.67" r="69.15"/><line style="fill:none;stroke:#fff;stroke-linecap:round;stroke-linejoin:round;stroke-width:9px;" x1="129.68" y1="115.82" x2="193.11" y2="164.5"/></svg>
			</figure>
			<p>Find Jay-Be® retailers near you</p>
		</div>
		<div class="jbm__search">
			<div class="jbm__search__inner">

				<span>Within</span>

				<select name="radius" id="jbm__radius" @change="checkRadiusChanged($event)" v-model="radius">
					<option value="5" selected>5 miles</option>
					<option value="15">15 miles</option>
					<option value="25">25 miles</option>
					<option value="50">50 miles</option>
					<option value="100">100 miles</option>
					<option value="200">200 miles</option>
				</select>

				<span>of</span>

				<div class="jbm__search__postcode">
					<input type="text" name="postcode" v-model="postcode" v-on:keyup="checkPostcodeChanged" placeholder="Postcode">
				</div>

				<button type="button" name="search" v-on:click="search" :disabled="disableSearch == 1">Search</button>

				<p id="jbm__location" v-on:click="findLocation">
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" width="18" height="18"><path d="M504 240h-56.81C439.48 146.76 365.24 72.52 272 64.81V8c0-4.42-3.58-8-8-8h-16c-4.42 0-8 3.58-8 8v56.81C146.76 72.52 72.52 146.76 64.81 240H8c-4.42 0-8 3.58-8 8v16c0 4.42 3.58 8 8 8h56.81c7.71 93.24 81.95 167.48 175.19 175.19V504c0 4.42 3.58 8 8 8h16c4.42 0 8-3.58 8-8v-56.81c93.24-7.71 167.48-81.95 175.19-175.19H504c4.42 0 8-3.58 8-8v-16c0-4.42-3.58-8-8-8zM256 416c-88.22 0-160-71.78-160-160S167.78 96 256 96s160 71.78 160 160-71.78 160-160 160zm0-256c-53.02 0-96 42.98-96 96s42.98 96 96 96 96-42.98 96-96-42.98-96-96-96zm0 160c-35.29 0-64-28.71-64-64s28.71-64 64-64 64 28.71 64 64-28.71 64-64 64z" fill="#1a9cb6" /></svg>
					Use my current location
				</p>
			</div>

		</div>

		<Results v-if="gotResults"
			v-bind:stockistResults="resultData"
			v-bind:loadingCheck="loadingCheck"
			v-on:emitStockistTarget="emitStockistTarget"
			></Results>
	</div>
</template>

<script>

import Results from './Results.vue'

import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(VueAxios, axios)

export default {
	name: 'Sidebar',
	props: {
		triggerAll: Boolean,
	},
	components: {
		Results
	},
	data: function() {
		return {
			radius: 5,
			postcode: '',
			previousPostcode: '',
			previousRadius: 0,
			geolocation: {
				latitude: '',
				longitude: ''
			},
			url: process.env.VUE_APP_ASSET_PATH + '/_/importer/get_stores_object.php',
			locationSearching: false,
			disableSearch: false,
			gotResults: false,
			resultData: [],
			loadingCheck: false
		}
	},
	watch: {
		triggerAll: function(newVal, oldVal) { // watch it
			if (newVal == true) {
				this.showAll();
			}
		}
	},
	methods: {
		showAll: function() {
			var self = this;
			var url = self.url;

			axios.post(url, {
				radius: 99999999,
				lat: 53.924606,
				lng: -1.821216
			})
			.then(function (response) {
				if (response.status == 200) {
					self.resultData = response.data;
					self.$emit('emitStockistData', self.resultData);
					self.gotResults = true;
				}
			})
			.catch(function (error) {
				console.log(error);
			});
		},
		search: function() {
			var regex = new RegExp('[^a-zA-Z0-9]', 'g');
			var cleaned = this.postcode.replace(regex, '');

			if (cleaned != '') {
				this.previousPostcode = cleaned;
				this.disableSearch = true;

				// this.disableSearch = false;
				this.validatePostcode(cleaned);
			}
		},
		checkPostcodeChanged: function() {
			if (this.postcode != this.previousPostcode) {
				this.disableSearch = false;
			} else {
				this.disableSearch = true;
			}
		},
		checkRadiusChanged: function() {
			if (this.radius != this.previousRadius) {
				this.disableSearch = false;
			} else {
				this.disableSearch = true;
			}
		},
		validatePostcode: function(postcode) {
			var self = this;
			axios.get('https://api.postcodes.io/postcodes/' + postcode)
			.then(function (response) {
				if (response.data.status == 200) {
					self.geolocation.latitude = response.data.result.latitude;
					self.geolocation.longitude = response.data.result.longitude;

					self.requestResults();
				}
			})
			.catch(function (error) {
				// handle error
				console.log(error);
			})
			.then(function () {
				// always executed
			});
		},
		requestResults: function () {
			var self = this;
			var url = self.url;

			axios.post(url, {
				radius: this.radius,
				lat: this.geolocation.latitude,
				lng: this.geolocation.longitude
			})
			.then(function (response) {
				if (response.status == 200) {
					self.resultData = response.data;
					self.$emit('emitStockistData', self.resultData);
					self.gotResults = true;
				}
			})
			.catch(function (error) {
				console.log(error);
			});

		},
		emitStockistTarget: function(target) {
			this.$emit('emitStockistTarget', target);
		},
		findLocation: function() {
			var self = this;

			if (navigator.geolocation) {
				if (this.locationSearching == false) {
					// Only search if not already searching
					if (this.previousPostcode == '' || this.postcode != this.previousPostcode) {
						// Search if not the same postcode as before and previous isn't empty

						// console.log('finding location');
						this.locationSearching = true;

						var position = navigator.geolocation.getCurrentPosition(function(position) {

							self.loadingCheck = true;

							self.geolocation.latitude = position.coords.latitude;
							self.geolocation.longitude = position.coords.longitude;

							// Get Postcode from geolocation
							axios.get('https://api.postcodes.io/postcodes', {
								params: {
									lon: self.geolocation.longitude,
									lat: self.geolocation.latitude
								}
							})
							.then(function (response) {
								// console.log('postcode lookup complete');
								if (response.status == 200) {
									self.previousPostcode = response.data.result[0].postcode;
									self.postcode = response.data.result[0].postcode;

									self.disableSearch = true;
									self.loadingCheck = false;

									self.requestResults();
								}
							})
							.catch(function (error) {
								// handle error
								console.log(error);
							})
							.then(function () {
								// always executed
								// Allow another location search to be done
								self.locationSearching = true;
							});
						});

					}
				}
			} else {
				window.alert('Geolocation is not supported by this browser, please enter a postcode.')
			}
		}
	}
}
</script>

