<template>
	<div class="map__wrap">
		<div id="map"></div>

		<div class="map__popups">
			<div
				v-for="(data, index) in stockistData"
				class="map__popups__item"
				v-bind:id="('popup_' + index)"
			>
			<span class="close" v-on:click="closePopup">
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 15.03 15.03" width="10" height="10"><path style="fill:none;stroke:#39434e;stroke-linecap:square;stroke-linejoin:round;stroke-width:1.5px;" d="M1.06 1.06l12.91 12.91M1.06 13.97L13.97 1.06"/></svg>
			</span>
			<div class="tab_outer">
				<div class="map__popups__item__tabs">
					<span v-on:click="changeTab" class="active">Address</span>
					<span v-on:click="changeTab">Products</span>
				</div>
				<div class="map__popups__item__tabs__content">
					<div class="tab address active">
						<p class="map__popups__item__name">{{ data.retailerData.name }}</p>
						<p class="map__popups__item__address" v-html="data.retailerData.address"></p>
						<p class="map__popups__item__tel" v-if="data.retailerData.phone">Tel: {{ data.retailerData.phone }}</p>

						<div class="map__popups__item__links">
							<a v-bind:href="directionLink(data.latitude, data.longitude)" target="_blank">Get directions</a>
							<a v-if="(data.retailerData.website != '')" v-bind:href="(data.retailerData.website)" target="_blank">Go to website</a>
						</div>
					</div>
					<div class="tab products">

						<div v-if="(data.ranges.CatFolding == 1)" class="folding">
							<img v-bind:src="(path + '/_/img/support/where-to-buy/gmap/cat-folding.png' )" alt="Folding Beds">
							<span>Folding Guest<br>Beds</span>
						</div>
						<div v-if="(data.ranges.CatSofa == 1)" class="sofa">
							<img v-bind:src="(path + '/_/img/support/where-to-buy/gmap/cat-sofas.png')" alt="Sofas & Sofa Beds">
							<span>Sofas &amp; <br>Sofa Beds</span>
						</div>
						<div v-if="(data.ranges.CatChildren == 1)" class="children">
							<img v-bind:src="(path + '/_/img/support/where-to-buy/gmap/cat-children.png')" alt="Children's Mattresses">
							<span>Childrens <br> Mattresses</span>
						</div>

					</div>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>

import gmapsInit from '../utils/gmaps';
import MarkerClusterer from '../../node_modules/@google/markerclusterer/src/markerclusterer.js';

import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';

Vue.use(VueAxios, axios);

export default {
	name: 'Gmap',
	props: {
		stockistData: Array,
	},
	data: function() {
		return {
			map: {},
			labelIndex: 0,
			stockists: [],
			allMarkers: [],
			markerCluster: {},
			url: process.env.VUE_APP_ASSET_PATH + '/_/importer/get_stores_object.php',
			path: process.env.VUE_APP_ASSET_PATH,
			google: '',
			mapPin: '/_/img/support/where-to-buy/gmap/small_circle.png',
			activeMapPin: '/_/img/support/where-to-buy/gmap/map_pin.png',
			clusterPin: '/_/img/support/where-to-buy/gmap/circle.png',
			breakpoint: 720,
			width: 0
		}
	},
	async mounted() {
		try {
			this.google = await gmapsInit();

			this.windowWidth();
			window.addEventListener('resize', this.windowWidth);

			this.loadGmap();

			// this.$nextTick(() => {
			// this.showAll();
			// })

		} catch (error) {
			console.error(error);
		}
	},
	computed: {
		updateStockistData: function() {
			this.loadedStockists = true;
		},
	},
	updated: function () {
		this.$nextTick(function () {
			// Code that will run only after the entire view has been re-rendered
			this.processStockistData();
		})
	},
	watch: {
		stockistData: function() { // watch it (newVal, oldVal)
			this.updateStockistData;
		}
	},
	methods: {
		windowWidth: function() {
			this.width = window.innerWidth;
		},
		showAll: function() {
			this.$emit('triggerAll', true);
		},
		directionLink: function(latitude, longitude) {
			var url = encodeURI("https://www.google.com/maps/dir/?api=1&destination=" + latitude + "," + longitude);
			return url;
		},
		gotoMarker: function(marker) {
			var thisMarker = marker.getPosition();
			var offset = -Math.abs(document.getElementsByClassName('jbm__rhs')[0].offsetWidth);
			var resultDivs = [...document.getElementsByClassName('map__results__item')];

			this.map.setZoom(15);
			this.offsetCenter(thisMarker, offset/2, -100);

			// Remove all active classes
			for (var i = 0; i < resultDivs.length; i++) {
				if (resultDivs[i].classList.contains('active')) {
					resultDivs[i].classList.remove('active');
				}
			}

			var clickedIndex = marker.getLabel().text;
			document.getElementById('result_' + (clickedIndex - 1)).classList.add('active');

			// Animate Marker
			marker.setAnimation(self.google.maps.Animation.BOUNCE);
			var timeout = setTimeout(function() {
				marker.setAnimation(null);
				clearTimeout(timeout);
			}, 1200);

			var scrollDistance = document.getElementById('result_' + (clickedIndex - 1)).offsetTop;
			// document.getElementsByClassName('jbm__sidebar__results__inner')[0].scrollTop = scrollDistance;

			var scrolledElem = document.getElementsByClassName('jbm__sidebar__results__inner')[0];

			// console.log(document.getElementById('result_' + (clickedIndex - 1)));
			// console.log('currentpos: ' + scrolledElem.scrollTop + ' scrollDistance: ' + scrollDistance);

			function getRelativePos(elm){
				var pPos = elm.parentNode.getBoundingClientRect(), // parent pos
				cPos = elm.getBoundingClientRect(), // target pos
				pos = {};

				pos.top    = cPos.top    - pPos.top + elm.parentNode.scrollTop,
				pos.right  = cPos.right  - pPos.right,
				pos.bottom = cPos.bottom - pPos.bottom,
				pos.left   = cPos.left   - pPos.left;

				return pos;
			}

			function scrollTo(element, to, duration, onDone) {
				var start = element.scrollTop,
				change = to - start,
				startTime = performance.now(),
				val, now, elapsed, t;

				function animateScroll(){
					now = performance.now();
					elapsed = (now - startTime)/1000;
					t = (elapsed/duration);

					element.scrollTop = start + change * easeInOutQuad(t);

					if ( t < 1 ) {
						window.requestAnimationFrame(animateScroll);
					} else {
						onDone && onDone();
					}
				}

				animateScroll();
			}

			function easeInOutQuad(t){ return t<.5 ? 2*t*t : -1+(4-2*t)*t }

			scrollTo(scrolledElem, scrollDistance, 0.25);


			// Add active class to result
		},
		changeTab: function(e) {
			var targetTab = e.target.outerText;
			var parent = e.srcElement.offsetParent;
			var tabs = parent.querySelectorAll('.tab');
			var tabButtons = parent.querySelectorAll('.map__popups__item__tabs span');
			var tab = parent.querySelector('.' + targetTab.toLowerCase());

			for (var i = 0; i < tabButtons.length; i++) {
				tabButtons[i].classList.remove('active');
			}

			for (var ii = 0; ii < tabs.length; ii++) {
				tabs[ii].classList.remove('active');
			}

			tab.classList.add('active');
			e.srcElement.classList.add('active');
		},
		closePopup: function(e) {
			var parent = this.findAncestor(e.target, 'popup-bubble-anchor');
			parent.style.display = 'none';

			var resultDivs = [...document.getElementsByClassName('map__results__item')];

			// Remove all active classes
			for (var i = 0; i < resultDivs.length; i++) {
				if (resultDivs[i].classList.contains('active')) {
					resultDivs[i].classList.remove('active');
				}
			}
			this.resetMarkerColours();
		},
		closeAllPopups: function() {
			var popups = document.getElementsByClassName('map__popups__item');
			for (var i = 0; i < popups.length; i++) {
				popups[i].parentNode.style.display = 'none';
			}
		},
		resetMarkerColours: function() {
			var self = this;
			var markers = this.allMarkers;
			for (var i = 0; i < markers.length; i++) {
				markers[i].setIcon(process.env.VUE_APP_ASSET_PATH + self.mapPin);
			}
		},
		gotoStockist: function(target) {
			var self = this;

			this.resetMarkerColours();
			this.closeAllPopups();

			var popupPosition = parseInt(target.replace('popup_', ''));

			// Set active icon
			this.allMarkers[popupPosition].setIcon(process.env.VUE_APP_ASSET_PATH + self.activeMapPin);

			// Open relevant popup
			// var popup = document.getElementById(target);
			// popup.parentNode.style.display = 'block';

			// Navigate to Marker
			// var thisMarker = this.allMarkers[popupPosition].getPosition();
			this.gotoMarker(this.allMarkers[popupPosition]);
			// var offset = -Math.abs(document.getElementsByClassName('jbm__rhs')[0].offsetWidth);

		},
		loadGmap: function() {
			var self = this;
			// In the following example, markers appear when the user clicks on the map.

			this.map = new this.google.maps.Map(document.getElementById('map'), {
				styles: [
					{
						"featureType": "all",
						"elementType": "labels.text.fill",
						"stylers": [
							{
								"saturation": 36
							},
							{
								"color": "#333333"
							},
							{
								"lightness": 40
							}
						]
					},
					{
						"featureType": "all",
						"elementType": "labels.text.stroke",
						"stylers": [
							{
								"visibility": "on"
							},
							{
								"color": "#ffffff"
							},
							{
								"lightness": 16
							}
						]
					},
					{
						"featureType": "all",
						"elementType": "labels.icon",
						"stylers": [
							{
								"visibility": "off"
							}
						]
					},
					{
						"featureType": "administrative",
						"elementType": "geometry.fill",
						"stylers": [
							{
								"color": "#fefefe"
							},
							{
								"lightness": 20
							}
						]
					},
					{
						"featureType": "administrative",
						"elementType": "geometry.stroke",
						"stylers": [
							{
								"color": "#fefefe"
							},
							{
								"lightness": 17
							},
							{
								"weight": 1.2
							}
						]
					},
					{
						"featureType": "landscape",
						"elementType": "geometry",
						"stylers": [
							{
								"color": "#f5f5f5"
							},
							{
								"lightness": 20
							}
						]
					},
					{
						"featureType": "poi",
						"elementType": "geometry",
						"stylers": [
							{
								"color": "#f5f5f5"
							},
							{
								"lightness": 21
							}
						]
					},
					{
						"featureType": "poi.park",
						"elementType": "geometry",
						"stylers": [
							{
								"color": "#dedede"
							},
							{
								"lightness": 21
							}
						]
					},
					{
						"featureType": "road.highway",
						"elementType": "geometry.fill",
						"stylers": [
							{
								"color": "#ffffff"
							},
							{
								"lightness": 17
							}
						]
					},
					{
						"featureType": "road.highway",
						"elementType": "geometry.stroke",
						"stylers": [
							{
								"color": "#ffffff"
							},
							{
								"lightness": 29
							},
							{
								"weight": 0.2
							}
						]
					},
					{
						"featureType": "road.arterial",
						"elementType": "geometry",
						"stylers": [
							{
								"color": "#ffffff"
							},
							{
								"lightness": 18
							}
						]
					},
					{
						"featureType": "road.local",
						"elementType": "geometry",
						"stylers": [
							{
								"color": "#ffffff"
							},
							{
								"lightness": 16
							}
						]
					},
					{
						"featureType": "transit",
						"elementType": "geometry",
						"stylers": [
							{
								"color": "#f2f2f2"
							},
							{
								"lightness": 19
							}
						]
					},
					{
						"featureType": "water",
						"elementType": "geometry",
						"stylers": [
							{
								"color": "#e9e9e9"
							},
							{
								"lightness": 17
							}
						]
					},
					{
						"featureType": "water",
						"elementType": "geometry.fill",
						"stylers": [
							{
								"color": "#049cb8"
							}
						]
					},
					{
						"featureType": "water",
						"elementType": "labels",
						"stylers": [
							{
								"color": "#e8e7e7"
							}
						]
					}
				],
				zoom: 6,
				center: {
					lat: 53.828554,
					lng: -3.696404
				},
				disableDefaultUI: true
			});

			var offset = document.getElementsByClassName('jbm__rhs');
			this.map.panBy(offset[0].offsetWidth/2, -100);

			// Add a marker clusterer to manage the markers.
			this.markerCluster = new MarkerClusterer(this.map, this.allMarkers, {
				// imagePath: 'https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/m',
				zoomOnClick: false,
				styles: [{
					url: process.env.VUE_APP_ASSET_PATH + self.clusterPin,
					width: 29,
					height: 29,
					textColor: 'white'
					//"Poppins", sans-serif
				}]
			});

			// Add arker clusterer custom zoom on click
			this.google.maps.event.addListener(self.map, 'clusterclick', function(cluster){
				var offset = -Math.abs(document.getElementsByClassName('jbm__rhs')[0].offsetWidth);
				self.map.fitBounds(cluster.bounds_);
				self.offsetCenter(self.map.getCenter(), offset/2, -100);
			});

			this.google.maps.event.addListenerOnce(self.map, 'bounds_changed', function(){
				// do something only the first time the map is loaded
				self.showAll();
			});
		},
		addMarker: function(location, map) {
			var self = this;

			this.labelIndex++;
			// Add the marker at the clicked location, and add the next-available label
			// from the array of alphabetical characters.
			var marker = new self.google.maps.Marker({
				position: location,
				label: {
					text: this.labelIndex.toString(),
					color: '#fff',
					fontSize: '0px'
				},
				map: map,
				icon: {
					url: process.env.VUE_APP_ASSET_PATH + self.mapPin
				}
			});
			marker.addListener('click', function() {
				// Close all popups and reset icon
				self.closeAllPopups();
				self.resetMarkerColours();

				// Go to marker
				self.gotoMarker(marker);

				// Set active icon
				marker.setIcon(process.env.VUE_APP_ASSET_PATH + self.activeMapPin);

				// Animate Marker
				// if (marker.getAnimation() !== null) {
				// 	marker.setAnimation(null);
				// } else {
				// marker.setAnimation(null);
				// marker.setAnimation(self.google.maps.Animation.DROP);

				// 	 setTimeout(function(){
				// 		 marker.setAnimation(null);
				// 	 }, 200);
				// }

				// Open relevant popup
				var id = parseInt(marker.label.text) - 1;
				var popup = document.getElementById('popup_' + id);
				// popup.style.display = 'block';
				popup.parentNode.style.display = 'block';
			});
			this.allMarkers.push(marker);
		},
		offsetCenter: function(latlng, offsetx, offsety) {

			// latlng is the apparent centre-point
			// offsetx is the distance you want that point to move to the right, in pixels
			// offsety is the distance you want that point to move upwards, in pixels
			// offset can be negative
			// offsetx and offsety are both optional

			// Prevent X offset if breakpoint is reached but keep the Y offset to compensate for the popup
			if (this.width <= this.breakpoint) {
				offsetx = 0;
			}

			var scale = Math.pow(2, this.map.getZoom());
			var worldCoordinateCenter = this.map.getProjection().fromLatLngToPoint(latlng);
			var pixelOffset = new this.google.maps.Point((offsetx/scale) || 0,(offsety/scale) ||0);

			var worldCoordinateNewCenter = new this.google.maps.Point(
				worldCoordinateCenter.x - pixelOffset.x,
				worldCoordinateCenter.y + pixelOffset.y
			);

			var newCenter = this.map.getProjection().fromPointToLatLng(worldCoordinateNewCenter);

			this.map.setCenter(newCenter);

		},
		createPopupClass: function() {
			var self = this;

			function Popup(position, content) {
				this.position = position;

				content.classList.add('popup-bubble');

				// This zero-height div is positioned at the bottom of the bubble.
				var bubbleAnchor = document.createElement('div');
				bubbleAnchor.classList.add('popup-bubble-anchor');
				bubbleAnchor.appendChild(content);

				// This zero-height div is positioned at the bottom of the tip.
				this.containerDiv = document.createElement('div');
				this.containerDiv.classList.add('popup-container');
				this.containerDiv.appendChild(bubbleAnchor);

				// Optionally stop clicks, etc., from bubbling up to the map.
				self.google.maps.OverlayView.preventMapHitsAndGesturesFrom(this.containerDiv);
			}
			// ES5 magic to extend google.maps.OverlayView.
			Popup.prototype = Object.create(self.google.maps.OverlayView.prototype);

			/** Called when the popup is added to the map. */
			Popup.prototype.onAdd = function() {
				this.getPanes().floatPane.appendChild(this.containerDiv);
			};

			/** Called when the popup is removed from the map. */
			Popup.prototype.onRemove = function() {
				if (this.containerDiv.parentElement) {
					this.containerDiv.parentElement.removeChild(this.containerDiv);
				}
			};

			/** Called each frame when the popup needs to draw itself. */
			Popup.prototype.draw = function() {
				var divPosition = this.getProjection().fromLatLngToDivPixel(this.position);

				// Hide the popup when it is far out of view.
				var display =
				Math.abs(divPosition.x) < 4000 && Math.abs(divPosition.y) < 4000 ?
				'block' :
				'none';

				if (display === 'block') {
					this.containerDiv.style.left = divPosition.x + 'px';
					this.containerDiv.style.top = divPosition.y + 'px';
				}
				if (this.containerDiv.style.display !== display) {
					this.containerDiv.style.display = display;
				}
			};

			return Popup;
		},
		processStockistData: function() {
			// Remove Markers from Map
			for (var i = 0; i < this.allMarkers.length; i++) {
				this.allMarkers[i].setMap(null);
			}

			this.allMarkers = [];
			this.labelIndex = 0;

			var stockists = this.stockistData;
			var self = this;
			var popup, Popup;
			var markerBounds = new self.google.maps.LatLngBounds();

			var offset = -Math.abs(document.getElementsByClassName('jbm__rhs')[0].offsetWidth);

			var counter = 0;
			for (var property in stockists) {
				var location = new self.google.maps.LatLng(stockists[property].latitude, stockists[property].longitude);
				this.addMarker(location, this.map);
				markerBounds.extend(location);

				// console.log('popup content', document.getElementById('popup_' + counter));

				Popup = this.createPopupClass();
				popup = new Popup(location, document.getElementById('popup_' + counter));
				popup.setMap(this.map);

				counter++;
			}

			// Clear and update Marker Clusters
			this.markerCluster.clearMarkers();
			this.markerCluster.addMarkers(this.allMarkers);

			// Center and offset map
			self.map.fitBounds(markerBounds);
			self.offsetCenter(self.map.getCenter(), offset/2, 0);
			// self.map.setZoom(self.map.getZoom()-1);
		},
		findAncestor: function(el, cls) {
			while ((el = el.parentElement) && !el.classList.contains(cls));
			return el;
		}
	}
}
</script>

