<script>

export default {
	name: 'Results',
	props: {
		stockistResults: Array,
		loadingCheck: Boolean
	},
	data: function() {
		return {
			stockistResultData: this.stockistResults,
			resultData: this.stockistResults,
			loadedStockists: false,
			loading: false,
			path: process.env.VUE_APP_ASSET_PATH,
			breakpoint: 720,
			width: 0
		}
	},
	watch: {
		// stockistResults: function(newVal, oldVal) {
		// 	// console.log('Prop changed: ', newVal, ' | was: ', oldVal);
		// 	// this.resultData = newVal;
		// },
		loadingCheck: function(newVal) { // newVal, oldVa
			this.loading = newVal;
		}
	},
	mounted: function() {
		this.$nextTick(() => {
			this.resultHeight();
			window.addEventListener('resize', this.resultHeight);
		})

		this.windowWidth();
		window.addEventListener('resize', this.windowWidth);
	},
	updated: function() {
		// document.onreadystatechange = () => {
			// if (document.readyState == "complete") {
				this.resultHeight();
				// window.addEventListener('resize', this.resultHeight);
			// }
		// }
		// this.resultHeight();
	},
	beforeDestroy: function() {
		// Unregister the event listener before destroying this Vue instance
		window.removeEventListener('resize', this.resultHeight);
	},
	methods: {
		windowWidth: function() {
			this.width = window.innerWidth;
		},
		directionLink: function(latitude, longitude) {
			var url = encodeURI("https://www.google.com/maps/dir/?api=1&destination=" + latitude + "," + longitude);
			return url;
		},
		resultHeight: function() {
			var jbm__heading = document.getElementsByClassName('jbm__heading')[0].clientHeight;
			var jbm__search = document.getElementsByClassName('jbm__search')[0].clientHeight;
			var jbm__sidebar__results__heading = document.getElementsByClassName('jbm__sidebar__results__heading')[0].clientHeight;
			var all = document.getElementsByClassName('jbm__sidebar')[0].clientHeight;
			var results = document.getElementsByClassName('jbm__sidebar__results')[0];
			var resultInner = document.getElementsByClassName('jbm__sidebar__results__inner')[0];

			results.style.height = all - (jbm__heading + jbm__search ) + 'px';
			resultInner.style.height = all - (jbm__heading + jbm__search + jbm__sidebar__results__heading) + 'px';
		},
		clickResult: function(e) {
			var index;
			var resultDivs = [...document.getElementsByClassName('map__results__item')];

			// Remove all active classes
			for (var i = 0; i < resultDivs.length; i++) {
				if (resultDivs[i].classList.contains('active')) {
					resultDivs[i].classList.remove('active');
				}
			}

			if (e.target.classList.contains('map__results__item')) {
				index = e.target.dataset.target;
				// Add active class
				e.target.classList.add('active');
			} else {
				var ancestor = this.findAncestor(e.target, 'map__results__item');
				index = ancestor.dataset.target;
				// Add active class
				ancestor.classList.add('active');
			}

			if (this.width <= this.breakpoint) {
				var checkMoreInfo = this.findAncestor(e.target, 'map__results__item__info__trigger');
				var checkMoreInfoClose = this.findAncestor(e.target, 'close');

				if (checkMoreInfo == null) {
					if (checkMoreInfoClose != null && checkMoreInfoClose.classList.contains('close')) {
						return;
					}

					var trigger = document.getElementsByClassName('jbm__rhs')[0];
					trigger.classList.remove('jbm__rhs--open');
				}

			}

			this.$emit('emitStockistTarget', 'popup_' + index);
		},
		clickResultPopup: function(e) {
			// Find parent with the taget data attribute for the target popup
			var ancestor = this.findAncestor(e.target, 'map__results__item');
			ancestor.classList.add('pop');
		},
		clickClosePop: function(e) {
			// Find parent with the taget data attribute for the target popup
			var ancestor = this.findAncestor(e.target, 'map__results__item');
			ancestor.classList.remove('pop');
		},
		findAncestor: function(el, cls) {
			while ((el = el.parentElement) && !el.classList.contains(cls));
			return el;
		}
	}
}
</script>

