<template>
	<div id="app" class="jbm">
		<div v-if="(show == true)" class="jbm__inner">
			<Gmap v-bind:stockistData="stockists" v-on:triggerAll="emitTriggerAll" ref="clickedResult" />
			<div class="jbm__rhs">
				<figure class="jbm__rhs__resp_trigger" v-on:click="toggleSidebar">
					<svg class="jbm__rhs__resp_trigger__search" width="14" height="25" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100.27 175.58"><polyline class="arl-1" style="fill:none;stroke:#202020;stroke-width:9px;" points="97.2 172.29 6.6 87.79 97.2 3.29"/></svg>

					<svg class="jbm__rhs__resp_trigger__close" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 15.03 15.03" width="17" height="17"><path style="fill:none;stroke:#202020;stroke-linecap:square;stroke-linejoin:round;stroke-width:1.5px;" d="M1.06 1.06l12.91 12.91M1.06 13.97L13.97 1.06"/></svg>
				</figure>
				<Sidebar
					v-on:emitStockistData="updateGmap"
					v-on:emitStockistTarget="emitStockistTarget"
					v-bind:triggerAll="triggerAll"
					></Sidebar>
			</div>
		</div>

		<span id="triggerGmap" v-on:click="(show = !show)">Toggle</span>
	</div>
</template>

<script>
import Gmap from './components/Gmap.vue'
import Sidebar from './components/Sidebar.vue'

export default {
	name: 'app',
	components: {
		Gmap,
		Sidebar
	},
	data: function() {
		return {
			show: false,
			stockists: [],
			triggerAll: false,
		}
	},
	mounted: function() {
		// console.log(process.env.VUE_APP_ASSET_PATH)
	},
	methods: {
		updateGmap: function(data) {
			this.stockists = data;
		},
		emitStockistTarget: function(target) {
			this.$refs.clickedResult.gotoStockist(target);
		},
		emitTriggerAll: function(data) {
			this.triggerAll = data;
		},
		toggleSidebar: function(e) {
			var trigger = document.getElementsByClassName('jbm__rhs')[0];
			trigger.classList.toggle('jbm__rhs--open');
		}
	}
}
</script>

